.App{

    height: 100vh;
    width: 100%;
    position: fixed;
    top: 0%;
    left: 0%;
    overflow-y: scroll;
}
.LightTeheme{
    color: black;
    background-color: white;
}
.DarkTheme {
    color: white;
    background-color: rgb(38, 38, 38);
}

.DarkTheme a {
